import React from "react";
// import './project.css';
import web1 from '../../assets/img/education/hitk1.png'
import Card from "react-bootstrap/Card";
import web2 from '../../assets/img/education/vvmq.png'
const Education = () => {
  const [header] = React.useState({
    mainHeader: "Education",
    subHeading: "My Background",
    text:
      "A short intro to my education",
  });
 
  return (
    <div className="services-pr" id="educt">
      <div className="container">
        <div className="services__header">
          <div className="common">
            <h3 className="heading">{header.mainHeader}</h3>
            <h1 className="mainHeader">{header.subHeading}</h1>
            <p className="mainContent">{header.text}</p>
            <div className="commonBorder"></div>
          </div>
          <div className="education-info">
          <Card>
              <Card.Header as="h5" className="d-flex justify-content-center flex-wrap">
                <Card.Img  src={web1} alt="Accenture logo" />
              </Card.Header>
              
            </Card>
            {/* <img src={web1} alt="Heritage Institute of Technology" class="img-responsive"/> */}
                <h3>Heritage Institute of Technology</h3>
                <h5>Bachelor of Technology (B.Tech)</h5>
                <h6>2017 - 2020</h6>
                <p>Top Engineering college in West Bengal and Kolkata offering BTech, MTech and MCA programmes.
                    Accredited by NAAC and most of the eligible courses accredited by National Board of Accreditation</p>
            </div>
            <hr/>
        {/* <div className="education-info">
        <img src={web2} 
        // style="    width: 34rem;"
        alt="Vivekananda Vidya Mandir" className="img-responsive"/>
            <h3>Vivekananda Vidya Mandir | VVM</h3>
            <h5>Primary Education </h5>
            <h6>2003 - 2015</h6>
            <p>VVM (Vivekananda Vidya Mandir) is the Top Best School in Ranchi, Jharkhand, India.
               As leading school, they teaches us different skills.</p>
        </div> */}
        </div>
      </div>
    </div>
  );
};

export default Education;
